.imgDashFramework {
  /*border: 1px solid black;*/
  position: relative;
  display: grid;
  align-items: center;
  justify-items: center;
  max-width: 450px;
  width: 100%;
  justify-self: center;
}

.corner {
  width: 30px;
  height: 30px;
  position: absolute;
}

.top_left {
  border-top: 2px solid;
  border-left: 2px solid;
  top: 0;
  left: 0;
}

.top_right {
  border-top: 2px solid;
  border-right: 2px solid;
  top: 0;
  right: 0;
}

.imgDashFramework2 {
  min-width: 230px;
  height: 150px;
  position: relative;
  display: grid;
  align-items: center;
  justify-items: center;
  width: 0;
  /*opacity: 0.75;*/
}

.corner2 {
  width: 15px;
  height: 15px;
  position: absolute;
}

.reds {
  border-color: red !important;
  color: red !important;
}

.bottom_right {
  border-bottom: 2px solid;
  border-right: 2px solid;
  bottom: 0;
  right: 0;
}

.bottom_left {
  border-bottom: 2px solid;
  border-left: 2px solid;
  bottom: 0;
  left: 0;
}

.green {
  border-color: #129a8e;
  color: green !important;
}
.MarcoPolo {
  opacity: 0.5;
}

.textMarc {
  position: absolute;
  justify-self: center;
  bottom: -17px;
  margin: 0 !important;
  font-weight: bold;
  font-size: 14px;
}

.IconMarco {
  position: absolute;
  font-size: 25px;
  right: 17px;
  top: 17px;
  z-index: 2;
}

.SimpleSlider {
  /*height: 100%;*/
  position: relative;
}

.SimpleSliderItem {
  display: grid !important;
  align-items: center;
  justify-items: center;
  height: calc(100vh - 260px);
  width: 100%;
  margin: 0 0 30px 0;
  grid-template-rows: 60px 1fr 100px;
  max-width: 26vw;
}

.TitleSlider {
  margin-bottom: 0 !important;
  font-weight: bold;
  text-align: center;
  color: #464646;
  margin-top: 5vh;
}

.SimpleSliderItem h1 {
  color: #464646;
  margin-top: 3px !important;
}

.slick-slider {
  height: 100%;
}

.SimpleSliderItem p {
  /* margin: 7px 30px; */
  text-align: center;
  max-width: 600px;
  font-weight: bold;
  color: gray;
}

